export default {
	onLoad() {
		this.initdatetime();
	},
	data() {
		return {
			date:[],
			time:[],
			nowdate:'',
			nowtime:'',
			dateActive:0,
			timeActive:0,
			now:''
		}
	},
	methods: {
		initdatetime(){
			var date = new Date();
			var day = ("0" + (date.getDate())).slice(-2);
			this.nowdate = ("0" + (date.getMonth() + 1)).slice(-2)+'-'+day;
			console.log(this.nowdate);
			uni.request({
				url: this.apiServer+'/staff/timemanage/Initial',
				method: 'GET',
				data: {
					 date:this.nowdate,
					 st_id:uni.getStorageSync('STID')
				},
				success: res => {
					let data = res.data;
					this.date = data.date;
					this.time = data.time;
				},
				fail: () => {},
				complete: () => {}
			});

		},
		datef(days,dis,index){
			this.nowdate = days;
			this.dateActive = index;
			console.log(days);
			console.log(dis);
			uni.request({
				url: this.apiServer+'/staff/timemanage/clickTime',
				method: 'GET',
				data: {
					date:days,
					st_id:uni.getStorageSync('STID')
				},
				success: res => {
					this.time = res.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		timef(tims,dis,index){
			this.now = this.nowdate + ' ' + tims+':00';
			this.timeActive = index;
			uni.request({
				url: this.apiServer+'/staff/timemanage/timeman',
				method: 'GET',
				data: {
					time:this.now,
					st_id:uni.getStorageSync('STID')
				},
				success: res => {
					this.time[index].disabled = !this.time[index].disabled
				},
				fail: () => {},
				complete: () => {} 
			});
		}
	}
}
